import React, { useState, useEffect } from 'react';
import {
  faBriefcase,
  faPaperPlane,
  faHouseUser,
  faChartPie,
  faClone,
} from '@fortawesome/free-solid-svg-icons';
import { useHistory } from 'react-router-dom';
import { SideBar, Content, TopBar } from '../../components';
import api from '../../api';
import './style.css';

const companyComponent = [
  { title: '', url: '/Main', icon: faHouseUser },
  { title: '', url: '/Main/Product', icon: faPaperPlane },
  { title: '', url: '/Main/Info', icon: faChartPie },
  { title: '', url: '/Main/Product/:id', icon: faPaperPlane },
  { title: '', url: '/Main/Adjust', icon: faBriefcase },
  { title: '', url: '/Main/ManageOrder', icon: faChartPie },
  { title: '', url: '/Main/ManageRefund', icon: faClone },
];
const adminComponent = [
  { title: 'ADMIN ', url: '/Main/ManageProduct', icon: faHouseUser },
  { title: 'ADMIN ', url: '/Main/ManageAdjust', icon: faBriefcase },
  // { title: 'AMDIN ', url: '/Main/Product/:id', icon: faPaperPlane },
];

const Main: React.FC = () => {
  const [sidebarIsOpen, setSidebarOpen] = useState(true);
  const [isAdmin, setIsAdmin] = useState<any>(null);
  const toggleSidebar = () => setSidebarOpen(!sidebarIsOpen);
  const history = useHistory();

  useEffect(() => {
    const localAdmin = localStorage.getItem('isAdmin');
    if (localAdmin !== null) setIsAdmin(localAdmin);
  }, []);
  if (localStorage.getItem('token') === null) {
    const baseURL: any = api.getBaseURL();
    window.location.href = baseURL;
  }
  return isAdmin === '' ? (
    <div className="App wrapper">
      <SideBar toggle={toggleSidebar} isOpen={sidebarIsOpen} menu={companyComponent} />
      <div className="flex_vertical">
        <TopBar toggleSidebar={toggleSidebar} />
        <Content toggleSidebar={toggleSidebar} sidebarIsOpen={sidebarIsOpen} />
      </div>
    </div>
  ) : (
    <div className="App wrapper">
      <SideBar toggle={toggleSidebar} isOpen={sidebarIsOpen} menu={adminComponent} />
      <div className="flex_vertical">
        <TopBar toggleSidebar={toggleSidebar} />
        <Content toggleSidebar={toggleSidebar} sidebarIsOpen={sidebarIsOpen} />
      </div>
    </div>
  );
};

export default Main;
