const TopBarLibs = {
  Logout: (e?: any, history?: any) => {
    console.log(history);
    if (localStorage.getItem('isAdmin')) {
      // 
      localStorage.removeItem('isAdmin');
      localStorage.removeItem('token');
      history.push(`/`);
    } else {
      //  
      localStorage.removeItem('isAdmin');
      localStorage.removeItem('token');
      history.push(`/`);
    }
    if (history) history.push('/');
  },
  goToRegister: (history: any, url: string) => {
    history.push(url);
  },
};

export default TopBarLibs;
